<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示所有的平台活动信息</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>活动名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>活动时间</label>
        <cms-picker @change="setTimer"></cms-picker>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <cms-sticky>
      <section class="cms-batch">
        <cms-button
          v-prem="'/activity/save'"
          icon="add"
          type="success"
          size="small"
          @click="$routerLink('/activity/save','save')"
        >新增</cms-button>
        <cms-button
          icon="error"
          type="error"
          size="small"
          v-prem="'/activity/delete'"
          @click="deleteDatas(ids)"
        >批量删除</cms-button>
      </section>
    </cms-sticky>
    <el-table
      :data="tableData"
      style="width: 100%"
      stripe
      v-loading="loading"
      @selection-change="getCheckItems"
      @row-click="checkRows"
      ref="dataTable"
    >
      <el-table-column type="selection" width="46" fixed="left"></el-table-column>
      <el-table-column prop="name" label="活动名称" min-width="220">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.name">{{scope.row.name}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="discountAmount" label="活动时间" min-width="220">
        <template
          slot-scope="scope"
        >{{$moment(scope.row.promoteBeginTime,'YYYY-MM-DD')}} 至 {{$moment(scope.row.promoteEndTime,'YYYY-MM-DD')}}</template>
      </el-table-column>
      <el-table-column prop="discountAmount" label="报名开始时间" min-width="220">
        <div slot-scope="scope">
          <div
            v-if="scope.row.rule"
          >{{$moment(scope.row.rule.signBeginTime,'YYYY-MM-DD')}} 至 {{$moment(scope.row.rule.signEndTime,'YYYY-MM-DD')}}</div>
          <div v-else style="text-align:center">-</div>
        </div>
      </el-table-column>
      <el-table-column label="报名商家">
        <div slot-scope="scope">
          <span
            class="link"
            @click="$routerLink('/activity/store/list','list',
          {promoteId:scope.row.id,name:scope.row.name,signEndTime:scope.row.rule.signEndTime})"
          >查看</span>
        </div>
      </el-table-column>
      <el-table-column label="操作选项" width="240" fixed="right" align="right">
        <div slot-scope="scope">
          <cms-button
            v-if="canAdjust(scope.row)"
            icon="edit"
            size="small"
            type="info"
            v-prem="'/activity/update'"
            @click.stop="$routerLink('/activity/update','update',{id:scope.row.id})"
          >修改</cms-button>
          <cms-button
            v-if="overAdjust(scope.row)"
            icon="error"
            size="small"
            type="info"
            v-prem="'/activity/delete'"
            @click.stop="deleteData(scope.row.id)"
          >删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total="total"></cms-pagination>
  </div>
</template>
<script>
import {
  activityPage,
  activityDelete
} from './../../../api/marketing/index.js'
import list from '@/mixins/list'
var defaultData = {
  name: '',
  beginTime: '',
  endTime: '',
  promoteType: '',
  size: 10,
  page: '1'
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    setTimer(value) {
      this.queryParams.beginTime = value[0]
      this.queryParams.endTime = value[1]
      this.getTableData(activityPage, this.queryParams)
    },
    deleteData(id) {
      this.deleteTableData(activityDelete, id)
    },
    deleteDatas(ids) {
      this.deleteTableDatas(activityDelete, ids)
    },
    canAdjust(item) {
      // 控制是否显示修改和删除按钮
      const now = new Date().getTime() // 获取当前时间戳
      if (now > item.rule.signBeginTime) {
        // 若是处于报名阶段开始到活动结束阶段
        return false
      } else {
        return true
      }
    },
    overAdjust(item) {
      const time = new Date().getTime() // 获取当前时间戳
      if (time > item.rule.signEndTime || time < item.rule.signBeginTime) {
        // 若是处于报名阶段开始到活动结束阶段
        return true
      } else {
        return false
      }
    }
  },
  created() {
    this.getTableData(activityPage, this.queryParams)
  }
}
</script>

<style>
</style>



